.page {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__picture {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__header {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review-box {
  justify-content: space-between;
  align-items: flex-end;
  height: 16px;
  margin-top: 0;
  margin-bottom: 24px;
  display: flex;
}

.card__review-box-content {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-box {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 0;
  margin-bottom: 16px;
  display: flex;
}

.card__price-box .card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-box .card__price-content {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__purchase-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__purchase-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
